@import "../../utils/colorPalette";

#app { height: 100% }

.swiper {
  width: 100%;
  height: 100%;
  scroll-snap-align: start;
}

.mySwiper
{
  .swiper-wrapper{
    height: 100vh;
    width: 100vw;
  }
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;

  &.swiper-slide-active{
    width: 100vw;
  }

}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
